// Colors

:root {
  --c-bg: #fff;
  --c-fg: #000;
}

$c-bg: var(--c-bg);
$c-fg: var(--c-fg);
$gradient: linear-gradient(-45deg, rgba(255,255,0,1) 0%, rgba(255,0,255,1) 33%, rgba(0,255,255,1) 66%, rgba(255,255,0,1) 100%);


@function vw($vw) {
  @return calc(#{$vw / 100} * (100vw - 20px));
}

@mixin small {
  @media screen and (min-width: 380px) { @content; }
}

@mixin medium {
  @media screen and (min-width: 680px) { @content; }
}

@mixin large {
  @media screen and (min-width: 880px) { @content; }
}

body {
  margin: 0;
  font-family: 'Favorit', Helvetica, sans-serif;
  font-feature-settings: "ss03", "ss04", "zero";
  min-height: 100vh;
  display: grid;
  grid-template-rows: auto 1fr;
  overflow-x: hidden;
  background-color: $c-bg;
  color: $c-fg;

  a {
    color: currentColor;
    text-decoration: none;
    font-family: 'Favorit Lining', Helvetica, sans-serif;

    &:hover {
      font-family: 'Favorit', Helvetica, sans-serif;
    }
  }
}

header {
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 10px;
  padding: 10px;

  @include small {
    grid-template-columns: 0.618fr 1fr;
  }

  @include large {
    grid-template-columns: 0.618fr 1fr 1fr;
  }

  * {
    font-size: 13px;
    line-height: 1.2;
    font-weight: normal;
    margin: 0;
    padding: 0;

    @include medium {
      font-size: vw(1.25 * 1.618);
    }

    @include large {
      font-size: vw(1.25);
    }
  }

  .links {
    list-style: none;
    display: none;

    @include large {
      grid-template-columns: 0.618fr 1fr 1fr;
      display: block;
    }

    li {
      a {
        text-decoration: none;
        font-family: 'Favorit', Helvetica, sans-serif;

        &:hover {
          font-family: 'Favorit Lining', Helvetica, sans-serif;

            &::after {
              content: 'â†—';
            }
        }
      }
    }
  }
}

main {
  padding: 10px 0 10px 10px;
  display: grid;
  grid-template-columns: 1fr 1.618fr;
  grid-template-rows: 1fr auto;

  @include small {
    padding: 10px;
  }

  @include large {
    grid-template-columns: 1fr 1.618fr 1fr 0.618fr;
  }

  .intro {
    margin: 0;
    grid-column: 1 / span 2;
    grid-row: 2;
    font-size: 24px;
    line-height: 1.1;
    align-self: flex-end;

    @include small {
      font-size: 26px;
    }

    @include medium {
      font-size: vw(2.5 * 1.618);
    }

    @include large {
      font-size: vw(2.5);
    }
  }

  .gradient {
    grid-column: 4;
    grid-row: 2;
    border-radius: 800px 800px 0px 00px;
    background: $gradient;
    background-size: 100% 100%;
    background-position: center;
  }
}
